import { createStore } from "@stencil/store";
import { InfoBannerData, ReviewAverage, ReviewProduct } from "./common";
import { ProductCardData } from "./listing";

export const product = createStore({} as ProductData);

export interface ProductData {
    id: string;
    name: string,
    link: string;
    
    breadcrumbs: CategoryData[];
    categories: CategoryData[];

    brand: BrandData;
    brandName: string;
    description?: string;

    message?: string;

    attributes?: AttributeData[];

    currency?: string;
    previousPrice?: number;
    currentPrice: number;
    lowestPrice?: number;
    shippingPrice?: number;
    shippingTime: string;
    shippingMessage: string;

    availability: number;

    count: number,
    cartLoading?: boolean,
    favouritesLoading?: boolean,
    favouritesCompleted?: boolean,

    images?: ProductImageData[]
    traitIDs?: string;
    traits?: TraitData[]

    catalog?: string;
    model?: string;
    ean?: string;

    reviews ?: ReviewCollectionData;

    infoBanner ?: InfoBannerData;
    productBanner ?: ProductBannerData;

    history: string;

    warranty: string;
    warrantyLink: string;

    recycle: TooltipAttribute;
    points: PointData;
    negotiateEnabled: boolean;
    installments: InstallmentData;

    tags: LinkPair[];
    variants: VariantData[];

    tabs: TabData[];
    youtube: string[];

    comments: CommentData;

    similar: ProductCardData[];
    accessories: ProductCardData[];

    reviewAverage: ReviewAverage;
    reviewProducts: ReviewProduct[];

    strings: ProductStringData;
}

